@import '../../config.scss';

.orderConfirmation {
  .title {
    background-color: primaryColorDark;
    color: white;
    margin-right: 3rem;

    @include respond(phone) {
      margin-right: 0;
    }

    h2 {
      padding: 0.7rem;
    }
  }

  .shippingInfo {
    display: grid;
    grid-template-columns: 1fr 1fr;

    .info,
    .method {
      display: flex;
      flex-direction: column;
      grid-column: span 1;
      overflow-wrap: break-word;

      :first-child {
        padding: 0 0 0.5rem;
        font-weight: 600;
      }
    }
  }

  .payment {
    a {
      text-decoration: none;
      color: secondaryColor;
      font-size: 1.2rem;
    }

    .testCardInfo {
      color: secondaryColorDark;
      font-weight: 600;
    }
  }
}
